.sala-modern-carousel-style-04{
	.slide-overlay {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		height: 100%;
		background-color: rgba(51, 51, 51, 0.5);
		background-image: inherit;
	}
	.slide-content {
		align-items: center;
		justify-content: center;
	}
}

.sala-modern-carousel-style-05{
	.sala-box.slide-wrapper{
		display: flex;
		align-items: center;
		border: solid 1px #eeeeee;
		border-radius: 30px;
		padding: 30px;
		.sala-image{
			-webkit-box-flex: 0;
			-ms-flex: 0 0 60px;
			flex: 0 0 60px;
			max-width: 60px;
			margin-right: 25px;
			img{
				border-radius: 50%;
				width: 60px;
				height: 60px;
				object-fit: cover;
			}
		}
		.slide-content h3{
			margin-bottom: 0;
			font-size: 24px;
			font-weight: 500;
		}
	}
}
