.elementor-widget-sala-testimonial-grid {
	.testimonial-item {
		position: relative;
		display: inline-block;
		width: 100%;
		max-width: 100%;
		.flex{
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-bottom: 24px;
			.thumbnail{
				margin-bottom: 0;
			}
		}
	}

	&.align-left {
		text-align: left;
	}

	&.align-right {
		text-align: right;
	}

	.testimonial-main-content {
		display: flex;
		justify-content: center;
	}

	.content-wrap {
		position: relative;
		display: inline-block;
		width: 100%;
		max-width: 100%;
	}

	&.layout-image-inline {
		.avatar {
			flex-shrink: 0;
			margin: 0 20px 0 0;
		}

		.info {
			display: inline-flex;
			align-items: center;
			text-align: left;
		}

		&.align-right {
			.info {
				text-align: right;
				flex-direction: row-reverse;
			}

			.avatar {
				flex-shrink: 0;
				margin: 0 0 0 20px;
			}

			.testimonial-item {
				text-align: right;
			}
		}
	}

	&.layout-image-stacked {
		.avatar {
			margin: 0 0 28px;
		}

		.info {
			display: inline-block;
		}
	}

	&.layout-image-above {
		.avatar {
			margin-bottom: 20px;
		}

		.cite {
			display: block;
			margin-top: 46px;
		}
	}

	&.image-position-above {
		.info {
			padding: 0 0 19px;
		}
	}

	&.image-position-below {
		.info {
			padding: 19px 0 0;
		}
	}

	&.image-position-bottom {
		.testimonial-main-content {
			height: 100%;
		}

		.content-wrap {
			display: inline-flex;
			flex-direction: column;
			height: 100%;
			width: 100%;
		}

		.content {
			flex-grow: 1;
		}

		.info {
			padding: 60px 0 0;
			flex-shrink: 0;
		}

		&.align-center {
			.info {
				justify-content: center;
			}
		}
	}

	.thumbnail {
		margin-bottom: 38px;
	}

	.avatar {
		img {
			width: 90px;
			height: 90px;
			border-radius: 50%;
		}
	}

	.text {
		font-style: italic;
	}

	.cite {
		font-style: normal;
	}

	.title {
		font-size: 21px;
		margin-bottom: 14px;
	}

	.testimonial-rating {
		font-size: 12px;
		color: #f5a623;
		letter-spacing: 4px;
		margin-bottom: 10px;
	}

	.name {
		display: block;
		font-size: 18px;
		line-height: 1.34;
		margin-bottom: 0;
	}

	.position {
		display: block;
		font-size: 12px;
		font-weight: 500;
		text-transform: uppercase;
		letter-spacing: 1.38px;
		margin-top: 6px;
	}
}
